.contact {
    width: 100%;
    height: 90vh;
    display: flex;
}

.contact .leftSide {
    height: 100%;
    flex: 50%;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
}

.contact .rightSide {
    height: auto;
    flex: 50%;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.contact .rightSide h1 {
    font-family: "Trebuchet MS", "Lucida Sans Unicode", "Lucida Grande", "Lucida Sans", Arial, sans-serif;
    font-weight: 10;
    font-size: 50px;
    margin-left: 30px;
}

form {
    display: flex;
    flex-direction: column;
    width: auto;
    height: auto;
    padding: 30px;
}

form input {
    height: 40px;
    width: 80%;
    border: none;
    border-bottom: 1px solid #121619;
    color: black;
}

form textarea {
    margin-top: 15px;
    height: 70px;
    width: 80%;
    border: none;
    border-bottom: 1px solid #121619;
    color: black;
}

input::placeholder,
textarea::placeholder {
    font-weight: bold;
    color: black;
    font-family: Arial, Helvetica, sans-serif;
}

form input:focus,
textarea:focus {
    outline: none;
}

form label {
    margin-top: 15px;
    color: grey;
}

form button {
    margin-top: 40px;
    width: 140px;
    height: 40px;
    border: none;
    background-color: #121619;
    color: whitesmoke;
    font-size: 15px;
    cursor: pointer;
}

@media only screen and (max-width: 1360px) {
    .contact .leftSide {
        width: auto;
        flex: 50%;
        background-position: right;
        background-size: cover;
    }
    .contact .rightSide {
        display: flex;
        flex: 50%;
        flex-direction: column;
        align-items: center;
    }
    .contact .rightSide h1 {
        font-family: "Trebuchet MS", "Lucida Sans Unicode", "Lucida Grande", "Lucida Sans", Arial, sans-serif;
        font-weight: 8;
        font-size: 30px;
    }
    form button {
        display: inline;
        width: 100px;
        height: 40px;
        border: none;
        border-radius: 15px;
        background-color: #121619;
        color: whitesmoke;
        font-size: 12px;
        cursor: pointer;
    }
}